* {
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5 {
  font-size: 100%;
  font-weight: normal;
}
.wrap {
}
nav {
  background-color: rgb(6, 56, 133);
  color: #fff;
}
.list01 {
  display: flex;
  justify-content: space-between;
}
.like {
  cursor: pointer;
}
nav h1 {
  padding: 1rem;
  font-size: 2.3rem;
  font-weight: bold;
}
section.list {
  color: #535353;
  padding: 1.5rem;
  border-bottom: 1px solid #535353;
}
section.list div {
  font-weight: 600;
}
section.list p {
  margin-top: 0.3rem;
}
.modal {
  margin-top: 20px;
  padding: 20px;
  background: #eee;
  text-align: left;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid #cccccc;
  border-radius: 1rem;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.349);
}
.modal h2 {
  font-size: 1.8rem;
  font-weight: 600;
}
